.form-inline {
  display: flex;
}
.form-inline > * {
  display: inline-block;
}

.form-radio,
.form-checkbox {
  display: flex;
  position: relative;
  align-items: center;
  color: #666666;
  font-size: 14px;
  margin-right: 17px;
}
.form-radio input,
.form-checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0.1;
  overflow: hidden;
}
.form-radio span,
.form-checkbox span {
  display: inline-block;
  position: relative;
  vertical-align: middle;
  border: 1px solid #cccccc;
  width: 16px;
  height: 16px;
  margin-right: 6px;
  font-size: 0;
}
.form-radio span:after,
.form-checkbox span:after {
  content: "";
  position: absolute;
  display: inline-block;
  transition: all 0.1s;
  opacity: 0;
}

.form-checkbox span:after {
  background-position: 50% 50%;
}
.form-checkbox input:disabled + span:after {
  background-color: #e1e1e1;
  width: 8px;
  height: 8px;
  top: 3px;
  left: 3px;
  opacity: 1;
}
.form-checkbox input:checked + span:after {
  opacity: 1;
  background: url("../img/common/icon-checked.png") no-repeat 0 0;
  width: 12px;
  height: 9px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form-radio span {
  border-radius: 50%;
}
.form-radio input:disabled + span:after {
  background-color: #e1e1e1;
  width: 8px;
  height: 8px;
  top: 3px;
  left: 3px;
  border-radius: 50%;
  opacity: 1;
}
.form-radio input:checked + span:after {
  background-color: #264998;
  width: 8px;
  height: 8px;
  top: 3px;
  left: 3px;
  border-radius: 50%;
  opacity: 1;
}

.file_upload {
  position: relative;
}
.file_upload input[type=file] {
  opacity: 0.1;
  width: 1px;
  height: 1px;
  overflow: hidden;
  position: absolute;
}
.file_upload input[type=text] {
